							"frontmatter": {
								"title": "Book Review: Effective Javascript",
								"date": "2013-01-06 22:00:00+00:00",
								"path": "/2013/01/06/book-review-effective-javascript",
								"description": "A book review of Effective Javascript by David Herman",
								"category": "reviews",
								"dontfeature": null,
								"key": "effective-js"
							},
